// @computed : son pure actions, calculos en base a los datos de observable
const appState = new class AppState {
    @observable grammar = null;
    @observable textCode = '';
    @observable parseResult = [];
    @observable grammarList = [];
    @observable hasParseResult = false;

    @action("obtengo las gramticas del servidor")
        getGrammars = () => {
            debugger;
            fetch(buildUrl("grammars"))
                .then(r => r.json())
                .then(grammars => { 
                        debugger;
                        this.grammarList = grammars;
                    }
                );
    }

    @action("agrego una lnea de resultado") 
    setParseLine = line => {
        this.parseResult = [...appState.parseResult, line];
        this.hasParseResult = true;
    }

    @action("escribo cdigo")
    setCode = code => {
        this.textCode = code;
    }

    @action("establezco la gramtica a usar") 
    setGrammar = grammar => {
        this.grammar = grammar;
        this.parseResult = [];
        this.hasParseResult = false;
    }
}
export default appState;


